'use client'

import PaymentForm from '@/components/PaymentForm'
import ShippingForm from '@/components/ShippingForm'
import { cartItems } from '@/lib/constants'
import { ArrowRight, Trash2 } from 'lucide-react'
import Image from 'next/image'
import { useRouter, useSearchParams } from 'next/navigation'
import { useState } from 'react'

const steps = [
  { id: 1, title: 'Shopping Cart' },
  { id: 2, title: 'Shipping Address' },
  { id: 3, title: 'Payment Method' },
]

const CartPage = () => {
  const searchParams = useSearchParams()
  const router = useRouter()
  const [shippingForm, setShippingForm] = useState(null)

  const activeStep = parseInt(searchParams.get('step') || '1')
  return (
    <div className='flex flex-col gap-8 items-center justify-center mt-12 cursor-default'>
      {/* TOP  */}
      <h1 className='text-2xl font-medium '>Your Shopping Cart</h1>
      <div className='flex flex-col lg:flex-row in-checked:gap-8 lg:gap-16'>
        {steps.map((step) => (
          <div
            className={`flex items-center gap-2 border-b-2 pb-4 ${
              step.id === activeStep ? 'border-gray-800' : 'border-gray-200'
            }`}
            key={step.id}
          >
            <div
              className={`w-6 h-6 rounded-full text-white p-4 flex items-center justify-center ${
                step.id === activeStep ? 'bg-gray-800' : 'bg-gray-500'
              }`}
            >
              {step.id}
            </div>
            <p
              className={`text-sm font-medium ${
                step.id === activeStep ? 'text-gray-800' : 'text-gray-500'
              }`}
            >
              {step.title}
            </p>
          </div>
        ))}
      </div>
      {/* Steps & Details  */}
      <div className='w-full flex flex-col lg:flex-row gap-16 '>
        {/* Steps  */}
        <div className='w-full lg:w-7/12 shadow-lg border-gray-100 border-1 rounded-lg p-8 flex flex-col gap-8'>
          {activeStep === 1 ? (
            cartItems.map((item) => (
              // Single Cart Item
              <div className='flex items-center justify-between' key={item.id}>
                {/* Image and Details  */}
                <div className='flex gap-8'>
                  <div className='relative w-32 h-32 bg-gray-50 rounded-lg overflow-hidden'>
                    <Image
                      src={item.images[item.selectedColor]}
                      alt={item.name}
                      fill
                      className='object-contain'
                    />
                  </div>
                  <div className='flex flex-col justify-between'>
                    <div className='flex flex-col gap-1'>
                      <p className='text-sm font-medium'>{item.name}</p>
                      <p className='text-xs text-gray-500'>
                        Size: {item.selectedSize}
                      </p>
                      <p className='text-xs text-gray-500'>
                        Color: {item.selectedColor}
                      </p>
                      <p className='text-xs text-gray-500'>
                        Quantity: {item.quantity}
                      </p>
                    </div>
                    <p className='font-medium'>${item.price.toFixed(2)}</p>
                  </div>
                </div>
                {/* Delete  */}
                <button className='w-8 h-8 rounded-full bg-red-100 hover:bg-red-200 transition-all duration-300 text-red-400 flex items-center justify-center cursor-pointer'>
                  <Trash2 className='w-3 h-3' />
                </button>
              </div>
            ))
          ) : activeStep === 2 ? (
            <ShippingForm />
          ) : activeStep === 3 && shippingForm ? (
            <PaymentForm />
          ) : (
            <p className='text-sm text-gray-500'>
              Please complete the shipping details
            </p>
          )}
        </div>
        {/* Details  */}
        <div className='w-full lg:w-5/12 shadow-lg border-gray-100 border-1 rounded-lg p-8 flex flex-col gap-8 h-max'>
          <h2 className='font-semibold '>Cart Details</h2>
          <div className='flex justify-between'>
            <p className='text-sm text-gray-500'>Subtotal</p>
            <p className='font-medium'>
              $
              {cartItems
                .reduce((acc, item) => acc + item.price * item.quantity, 0)
                .toFixed(2)}
            </p>
          </div>
          <div className='flex justify-between'>
            <p className='text-sm text-gray-500'>Discount(10%)</p>
            <p className='font-medium'>$ 10</p>
          </div>
          <div className='flex justify-between'>
            <p className='text-sm text-gray-500'>Shipping Fee</p>
            <p className='font-medium'>$ 10</p>
          </div>
          <hr className='border-gray-200' />
          <div className='flex justify-between'>
            <p className=' text-gray-800 font-semibold'>Total</p>
            <p className='font-medium'>
              $
              {cartItems
                .reduce((acc, item) => acc + item.price * item.quantity, 0)
                .toFixed(2)}
            </p>
          </div>
          {activeStep === 1 && (
            <button
              className='w-full bg-gray-700 hover:bg-gray-900 transition-all duration-300 text-white p-2 rounded-lg cursor-pointer flex items-center justify-center gap-2'
              onClick={() => router.push('/cart?step=2', { scroll: false })}
            >
              Continue <ArrowRight className='w-3 h-3' />
            </button>
          )}
        </div>
      </div>
    </div>
  )
}

export default CartPage
